<template>
  <div class="hello">
    <el-container class="mybody">
      <el-header>Header</el-header>
      <el-container>
        <el-aside width="100px">Aside</el-aside>
        <el-main class="contentBox">
          <div class="echartbox">
            <!-- 顶部切换按钮组 -->
            <div class="tabsbox">
              <div class="lefttab">
                <div :class="['tab',currentleft==0?'active':'']" @click="changelefttype(0)">全店推广</div>
                <div :class="['tab',currentleft==1?'active':'']" @click="changelefttype(1)">单品推广</div>
              </div>
              <div class="righttab">
                <span :class="['tab',currentright==0?'active':'']" @click="changerighttype(0)">汇总</span>
                <span :class="['tab',currentright==1?'active':'']" @click="changerighttype(1)">天钻</span>
                <span :class="['tab',currentright==2?'active':'']" @click="changerighttype(2)">商家</span>
              </div>
            </div>

            <!-- 比较容器 -->
            <div class="comparebox">
              <div class="time">
                <span>今日 vs</span>
                <el-date-picker v-model="chosetime" type="date" placeholder="选择日期"></el-date-picker>
              </div>

              <div class="compares">
                <div
                  :class="['box',item.checked?'active':'']"
                  :id="index"
                  v-for="(item,index) in compareData"
                  :key="index"
                  @click="choseShowData(index,item.checked)"
                >
                  <div class="title">{{item.title}}</div>
                  <div class="today">{{item.today}}</div>
                  <div class="otherday" v-if="item.otherday!=''">{{item.otherday}}</div>
                  <div class="otherday" v-else>-</div>
                </div>
              </div>
            </div>

            <!-- 图表 -->
            <div class="echart-wrapper">
              <div class="echart" ref="myEchart"></div>
            </div>
          </div>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>
import echart from "echarts";
export default {
  name: "HelloWorld",
  data() {
    return {
      chosetime: "",
      currentleft: 0,
      currentright: 0,
      checknum: 0,
      checkindex: null,
      chart: null,
      compareData: [
        {
          title: "消耗(元)",
          today: "445",
          otherday: "",
          checked: false
        },
        {
          title: "CPC",
          today: "3",
          otherday: "",
          checked: false
        },
        {
          title: "展现量",
          today: "1005",
          otherday: "",
          checked: false
        },
        {
          title: "点击量",
          today: "478948",
          otherday: "",
          checked: false
        },
        {
          title: "点击率",
          today: "12%",
          otherday: "",
          checked: false
        },
        {
          title: "CPM",
          today: "123",
          otherday: "",
          checked: false
        }
      ]
    };
  },
  watch: {
    chosetime(val) {
      let _this = this;
      if (val != "" && val != null) {
        _this.compareData.forEach(function(item) {
          item.otherday = parseInt(Math.random() * 100 + 1);
        });
      } else {
        _this.compareData.forEach(function(item) {
          item.otherday = "";
        });
      }
    }
  },
  mounted() {
    let _this = this;
    _this.initChart();
  },
  methods: {
    // 初始化数据
    initChart() {
      let _this = this;
      _this.chart = echart.init(_this.$refs.myEchart);

      let option = {
        color: ['#c23531','#2f4554', '#61a0a8', '#d48265', '#91c7ae','#749f83',  '#ca8622', '#bda29a','#6e7074', '#546570', '#c4ccd3'],
        tooltip: {
          trigger: "axis",
          axisPointer: {
            // 坐标轴指示器，坐标轴触发有效
            type: "line" // 默认为直线，可选为：'line' | 'shadow'
          }
        },
        legend: {
          left: "left",
          data: ["今日消耗（元）", "点击量", "CPC", "展现量", "点击率"]
        },
        grid: {
          left: "3%",
          right: "4%",
          bottom: "3%",
          containLabel: true
        },
        xAxis: [
          {
            type: "category",
            data: [
              "00:00",
              "01:00",
              "02:00",
              "03:00",
              "04:00",
              "05:00",
              "06:00",
              "07:00",
              "08:00",
              "09:00",
              "10:00",
              "11:00",
              "12:00",
              "13:00",
              "14:00",
              "15:00",
              "16:00",
              "17:00",
              "18:00",
              "19:00",
              "20:00",
              "21:00",
              "22:00",
              "23:00"
            ],
            axisTick: {
              alignWithLabel: true
            }
          }
        ],
        yAxis: [
          {
            type: "value",
            axisLine: {
              //坐标轴轴线相关设置。
              show: false
            },
            splitLine: {
              //坐标轴在 grid 区域中的分隔线。
              lineStyle: {
                type: "dashed"
              }
            },
            axisTick: {
              //坐标轴刻度相关设置。
              show: false
            },
            axisLabel: {
              //坐标轴刻度标签的相关设置。
              show: false
            }
          }
        ],
        series: [
          {
            name: "今日消耗（元）",
            type: "bar",
            // barWidth: '60%',
            data: [100, 120, 130]
          },
          {
            name: "点击量",
            type: "line",
            // barWidth: '60%',
            markPoint: {
              symbol: "roundRect"
            },
            data: [
              100,
              120,
              130,
              100,
              105,
              64,
              94,
              57,
              86,
              48,
              100,
              110,
              105,
              88,
              94,
              52,
              38,
              79,
              84,
              100,
              123,
              90,
              94,
              80
            ]
          },
          {
            name: "CPC",
            type: "line",
            // barWidth: '60%',
            data: [
              3.88,
              4.1,
              5.38,
              2.83,
              3.14,
              2.86,
              3.78,
              3.33,
              3.4,
              3.31,
              4.15,
              4.4,
              3.94,
              4.03,
              3.91,
              4.58,
              3.44,
              4.18,
              3.67,
              4.42,
              3.36,
              3.63,
              3.69,
              0.0
            ]
          },
          {
            name: "展现量",
            type: "bar",
            // barWidth: '60%
            data: [
              3.88,
              4.1,
              5.38,
              2.83,
              3.14,
              2.86,
              3.78,
              3.33,
              3.4,
              3.31,
              4.15,
              4.4,
              3.94,
              4.03,
              3.91,
              4.58,
              3.44,
              4.18,
              3.67,
              4.42,
              3.36,
              3.63,
              3.69,
              0.0
            ]
          },
          {
            name: "点击率",
            type: "bar",
            // barWidth: '60%',
            markPoint: {
              symbol: "roundRect"
            },
            data: [
              100,
              120,
              130,
              100,
              105,
              64,
              94,
              57,
              86,
              48,
              100,
              110,
              105,
              88,
              94,
              52,
              38,
              79,
              84,
              100,
              123,
              90,
              94,
              80
            ]
          }
        ]
      };

      _this.chart.setOption(option);
    },
    // 改变左边的tab
    changelefttype(index) {
      let _this = this;
      _this.currentleft = index;
    },
    // 改变右边的tab
    changerighttype(index) {
      let _this = this;
      _this.currentright = index;
    },
    // 选择需要战士的数据
    choseShowData(index, state) {
      let _this = this;
      if (!state) {
        _this.checknum++;
        if (_this.checknum > 2) {
          _this.checknum = 3;
        }
      }
      if (state) {
        if (_this.checknum == 3) {
          _this.checknum = 1;
        } else {
          _this.checknum--;
        }
      }
      if (_this.checknum <= 2) {
        _this.compareData[index].checked = !_this.compareData[index].checked;
      }
      _this.checkindex = index;
    }
  }
};
</script>
<style scoped lang='scss'>
.el-header,
.el-footer {
  background-color: #fff;
  color: #333;
  text-align: center;
  line-height: 60px;
  box-shadow: 0 5px 5px rgba(0, 0, 0, 0.1);
}

.el-aside {
  background-color: #fff;
  color: #333;
  text-align: center;
  line-height: 200px;
  box-shadow: 5px 0px 5px rgba(0, 0, 0, 0.1);
}

.contentBox {
  background: #f5f5f5;
  padding: 40px;
  box-sizing: border-box;
  .echartbox {
    height: 100%;
    background: #fff;
    border-radius: 10px;
    box-sizing: border-box;
    padding: 20px;
    // 顶部切换按钮组
    .tabsbox {
      display: flex;
      align-items: center;
      justify-content: space-between;
      box-sizing: border-box;
      border-bottom: 1px solid #eee;
      .lefttab {
        display: flex;
        align-items: center;
        .tab {
          position: relative;
          width: 100px;
          height: 50px;
          line-height: 50px;
          text-align: center;
          cursor: pointer;
          &.active {
            color: blue;
            &::after {
              content: "";
              position: absolute;
              bottom: -1px;
              height: 2px;
              left: 0;
              width: 100%;
              background: blue;
            }
          }
        }
      }
      .righttab {
        font-size: 0;
        .tab {
          display: inline-block;
          width: 70px;
          font-size: 12px;
          text-align: center;
          height: 30px;
          line-height: 30px;
          box-sizing: border-box;
          border: 1px solid #eee;
          cursor: pointer;
          &.active {
            border-color: blue;
            color: blue;
          }
        }
      }
    }
    //顶部切换按钮组结束

    // 比较容器
    .comparebox {
      .time {
        padding: 15px 0;
        box-sizing: border-box;
      }
      .compares {
        display: flex;
        align-items: center;
        justify-content: space-between;
        .box {
          position: relative;
          width: 15%;
          height: 100px;
          box-sizing: border-box;
          border: none;
          border-radius: 3px;
          padding: 10px;
          display: flex;
          flex-direction: column;
          justify-content: space-between;
          &.active {
            border: 1px solid blue;
          }
          &.active::after {
            content: "";
            position: absolute;
            bottom: 0;
            right: 0;
            border: 5px solid blue;
            border-left-color: transparent;
            border-top-color: transparent;
          }
          .today,
          .otherday {
            color: blue;
            font-size: 18px;
          }
        }
      }
    }
    .echart-wrapper {
      height: 550px;
      .echart {
        height: 100%;
      }
    }
  }
}
</style>
<style>
.mybody {
  min-height: 100vh;
}
</style>



